.pull-refresh {
  user-select: none;

  .pull-refresh__track {
    position: relative;
  }

  .pull-refresh__head {
    width: 100%;
    height: 50px;
    left: 0;
    position: absolute;
    text-align: center;
    top: -50px;
    font-size: 14px;
    color: gray;
    line-height: 50px;
  }
}
